.event {
  padding-top: 45px;
  padding-bottom: 14px;
  margin: 0 auto;
  color: #283645;
  font-size: 18px;
  line-height: 30px;
}
.credentials__label {
  color: #d22856;
  font-size: 14px;
  padding-left: 22px;
}
.event__title {
  width: 280px;
  margin: 0 auto;
  text-align: center;
  font-size: 14px;
}
.event__titles {
  color: #d22856;
  font-size: 14px;
  margin: 0 auto 34px;
  width: 230px;
  line-height: 18px;
  text-align: center;
  font-weight: 400;
}
.credentials__break{
  display: block;
}
.event__add {
  display: none;
}
.credentials__data {
  border: 2px solid #e5e5e5;
  padding: 24px 24px 20px 20px;
  color: #283645;
  margin-top: 5px;
  width: 232px;
  outline: none;
  &::placeholder {
    color: #283645;
    line-height: 30px;
    font-size: 18px;
    font-family: "Open Sans", "Arial", sans-serif;
    font-weight: 400;
  }
  &:hover {
    border-color: #283645;
  }
  &:focus,
  &:active {
    border-color: #d22856;
  }
}
.emotions {
  padding-top: 35px;
}
.emotions__title {
  margin-bottom: 15px;
}
.travel,
.achievements {
  background-color: #f2f2f2;
  padding-top: 38px;

}
.achievements {
  padding-bottom: 1px;
  margin-bottom: -2px;
}
.travel {
  padding-bottom: 7px;

}
.credentials {
  padding-top: 32px;
  padding-bottom: 27px;
}
.event__list {
  width: 320px;
  margin: 0 auto;
}
.credentials__item {
  margin-bottom: 15px;
  margin-left: 20px;
}
.achievements__item {
  margin-bottom: 45px;
  padding-top: 7px;
  display: flex;
  margin-left: 15px;
}
.achievements__item:nth-last-child(1) {
  margin-bottom: 50px;
}
.achievements__label {
  cursor: pointer;
  font-size: 18px;
}
.event__titles--hide {
  display: none;
}
.achievements__checkbox {
  position: relative;
  width: 0;
  height: 17px;
  outline: none;
  margin-right: 71px;
  margin-top: -2px;
  &:checked::after {
    content: "";
    display: block;
    background: url("../img/icon-form-check.svg") no-repeat;
    background-position: center;
    background-color: white;
    width: 29px;
    height: 25px;
    padding: 5px 3px;
    top: 0;
    left: 0;
    border: 2px solid #e5e5e5;
    cursor: pointer;
  }
  &::after {
    content: "";
    display: block;
    width: 29px;
    height: 25px;
    padding: 5px 3px;
    top: 0;
    left: 0;
    border: 2px solid #e5e5e5;
    cursor: pointer;
    background-color: white;
  }
  &:hover::after {
    border-color: #283645;
  }
  &:active::after {
    border-color: #d22856;
  }
}
.travel__item {
  margin-bottom: 40px;
  margin-left: 16px;
}
.travel__label {
  cursor: pointer;
}
.travel__device {
  position: relative;
  width: 0;
  height: 17px;
  outline: none;
  margin-right: 62px;
  margin-top: 10px;
  margin-left: 6px;
  &:checked::after {
    content: "";
    display: block;
    position: absolute;
    background-color: #d22856;
    border-radius: 50%;
    width: 15px;
    height: 15px;
    top: 0;
    left: 0;
    border: 10px solid white;
    cursor: pointer;
    margin-top: -6px;
  }
  &::after {
    content: "";
    display: block;
    position: absolute;
    background-color: white;
    border-radius: 50%;
    width: 15px;
    height: 15px;
    top: 0;
    left: 0;
    border: 10px solid white;
    cursor: pointer;
    margin-top: -6px;
  }
  &::before {
    position: absolute;
    content: "";
    display: block;
    background-color: #e5e5e5;
    border-radius: 50%;
    width: 39px;
    height: 39px;
    top: -2px;
    left: -2px;
    cursor: pointer;
    margin-top: -6px;
  }
  &:hover::before {
    background-color: #283645;
  }
  &:active::before {
    background-color: #d22856;
  }
}
.emotions {
  text-align: center;
}
.event__text {
  display: inline-block;
  height: 195px;
  width: 275px;
  border: 2px solid #e5e5e5;
  outline: none;
  margin-bottom: 5px;
  resize:vertical;
  &::placeholder {
    color: #283645;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 18px;
    line-height: 30px;
    width: 240px;
    padding-left: 20px;
    padding-top: 14px;
  }
  &:hover {
    border-color: #283645;
  }
  &:focus,
  &:active {
    border-color: #d22856;
  }
}
.event__button {
  display: block;
  border-radius: 3px;
  text-align: center;
  font-size: 18px;
  padding: 22px 37px 20px 33px;
  margin: 35px auto;
  width: 280px;
  font-weight: 700;
  background-color: #2aab6d;
  border: none;
  text-transform: uppercase;
  color: white;
  &:hover {
    background-color: #1c9b5e;
  }
  &:active {
    color: rgba(255, 255, 255, 0.3);
  }
  &:disabled {
    opacity: 0.3;
    background-color: #283645;
  }
}
.event__required {
  margin: 0 auto;
  width:  184px;
  text-transform: uppercase;
  font-size: 14px;
}
.event__star {
  color: #d22856;
}
.event__intro {
  display: none;
}
@-moz-document url-prefix() {
  .travel__label {
    position: relative;
  }
  .event__text::-moz-placeholder,
  .credentials__data::-moz-placeholder {
    color: #283645;
    opacity: 1;
  }
  .achievements__label {
    position: relative;
    margin-top: 4px;
    margin-bottom: -4px;
  }
  .achievements__checkbox {
    z-index: -1;
  }
  .achievements__checkbox {
    margin-right: 56px;
  }
  .achievements__checkbox:checked ~ .achievements__label {
    &::after {
      content: "";
      display: block;
      position: absolute;
      background: url("../img/icon-form-check.svg") no-repeat;
      background-position: center;
      background-color: white;
      width: 29px;
      height: 25px;
      padding: 5px 3px;
      top: -4px;
      left: -68px;
      border: 2px solid #e5e5e5;
      cursor: pointer;
    }
  }
  .achievements__checkbox ~ .achievements__label {
    &::after {
      content: "";
      display: block;
      position: absolute;
      width: 29px;
      height: 25px;
      padding: 5px 3px;
      top: -4px;
      left: -68px;
      border: 2px solid #e5e5e5;
      cursor: pointer;
      background-color: white;
    }
  }
  .travel__device:checked ~ .travel__label {
    &::after {
      content: "";
      display: block;
      position: absolute;
      background-color: #d22856;
      border-radius: 50%;
      width: 15px;
      height: 15px;
      top: 2px;
      left: -67px;
      border: 10px solid white;
      cursor: pointer;
      margin-top: -6px;
    }
  }
  .travel__device ~ .travel__label{
    &::after {
      content: "";
      display: block;
      position: absolute;
      background-color: white;
      border-radius: 50%;
      width: 15px;
      height: 15px;
      top: 2px;
      left: -67px;
      border: 10px solid white;
      cursor: pointer;
      margin-top: -6px;
    }}
  .travel__device ~ .travel__label {
    &::before {
      position: absolute;
      content: "";
      display: block;
      background-color: #e5e5e5;
      border-radius: 50%;
      width: 39px;
      height: 39px;
      top: 0px;
      left: -69px;
      cursor: pointer;
      margin-top: -6px;
    }
  }
  .travel__device ~ .travel__label {
    &:hover::before {
      background-color: #283645;
    }
    &:active::before {
      background-color: #d22856;
    }
  }
  .achievements__checkbox ~ .achievements__label {
    &:hover::after {
      border-color: #283645;
    }
    &:active::after {
      border-color: #d22856;
    }
  }
  .credentials__data {
    padding: 16px 24px 27px 20px;
  }
  .achievements {
    padding-bottom: 1px;
  }
  .travel__device {
    margin-right: 49px;
  }
  .event__text {
    padding-left: 16px;
    padding-top: 14px;
    height: 204px;
    box-sizing: border-box;
    margin-bottom: 8px;
  }
  .event__button {
    padding: 20px 20px 21px 20px;
    font-weight: 700;
  }
  .emotions {
    padding-top: 38px;
  }
}
