    padding-bottom: 20px;
  }
  .credentials__list-second{
    border: 2px solid #e5e5e5;
    width: 618px;
    padding-left: 43px;
    box-sizing: border-box;
    padding-top: 42px;
  }
  .achievements {
    position: relative;
    background-color: #fff;
    padding-left: 20px;
    padding-top: 47px;
  }
  .achievements__list {
    border: 2px solid #e5e5e5;
    width: 618px;
    box-sizing: border-box;
    padding-top: 47px;
    padding-left: 38px;
  }
  .achievements__checkbox {
    margin-right: 60px;
  }
  .achievements__label {
    padding-top: 1px;
  }
  .credentials__list-second .credentials__item {
    margin-bottom: 24px;
  }
  .credentials__label-second {
    display: flex;
    flex-direction: column-reverse;
    padding: 0;
    position: relative;
    width: 500px;
    &::before {
      content: "";
      position: absolute;
      width: 62px;
      height: 63px;
      background-repeat: no-repeat;
      background-position: center;
      background-color: #e5e5e5;
      z-index: 2;
      top: 5px;
      right: -15px;
    }
    &--phone::before {
      background-image: url("../img/icon-phone.svg");
      }
      &--mail::before {
        background-image: url("../img/icon-mail.svg");
        }
  }
  .credentials__data-second {
    width: 438px;
    margin-bottom: 9px;
    position: relative;
    padding: 22px 25px 20px 17px;
    min-height: 63px;
    box-sizing: border-box;
  }
  .credentials__mail,
  .credentials__phone {
    margin-left: 34px;
  }
  .event__titles--hide {
    display: block;
  }
  @-moz-document url-prefix() {
    .credentials__fix {
      vertical-align: bottom;
      display: inline-block;
      height: 45px;
    }
    .travel__device {
      margin-right: 40px;
    }
    .travel__device ~ .travel__label::before {
      top: -2px;
      left: -59px;
    }
    .travel__device:checked ~ .travel__label::after {
      top: 0px;
      left: -57px;
    }
    .travel__device ~ .travel__label::after {
      top: 0px;
      left: -57px;
    }
    .credentials__data-second {
      padding: 14px 25px 20px 17px;
    }
    .achievements__item {
      padding-top: 6px;
    }
    .achievements__checkbox {
      margin-right: 47px;
    }
    .achievements__checkbox:checked ~ .achievements__label::after,
    .achievements__checkbox ~ .achievements__label::after {
      left: -59px;
    }
    .achievements__list {
      padding-bottom: 6px;
      margin-bottom: 4px;
    }
    .event__button {
      margin-top: 43px;
    }
    .event__required {
      margin-top: 61px;
      margin-right: 17px;
    }
    .event__text {
      width: 500px;
      height: 191px;
      padding-left: 19px;
      padding-top: 12px;
      box-sizing: border-box;
      margin-bottom: 9px;
    }
    .emotions {
      margin-bottom: 24px;
    }
  }
}
