  }
  .event__required {
    margin-top: 51px;
    width: 320px;
  }
  .credentials__data-second {
    width: 315px;
    z-index: 2;
  }
  @-moz-document url-prefix() {
    .travel__device {
      margin-right: 38px;
    }
    .achievements__label {
      margin-top: 0px;
      margin-bottom: -1px;
    }
    .event__text {
      padding-left: 18px;
      width: 820px;
      height: 126px;
      margin-bottom: 9px;
    }
